import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';

const About = () => (
  <StaticQuery
    query={graphql`
      query {
        art_pb: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "pb" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        art_sl: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "sl" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        art_overview: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "overview" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="about">
        <Container>
          <Grid>
            <div>
            <h3>A problem happening to many hearing aids and cochlear implant users</h3>
              <p>
                It’s not likely that any styles of earbuds and earphones are going to work with hearing aids.<br/> 
                The reason is quite straightforward: hearing aids already sit within ears, so fitting another device into the ears doesn't work.
              </p>
            </div>
            <Art>
              <Img fluid={data.art_pb.childImageSharp.fluid} />
            </Art>
          </Grid>
          <Grid inverse>
            <Art>
              <Img fluid={data.art_sl.childImageSharp.fluid} />
            </Art>
            <div>
              <h3>The first earphone for all hearing conditions</h3>
              <p>
                We are launching a campaign for producing a brand new earphone design for you — no matter you are hearing normal, a hearing aid user, or a cochlear implant user.              
              </p>
            </div>
          </Grid>
          <Grid>
            <div>
              <h3>Hearing technology for everyone</h3>
              <p>
              Rearia is the first earphone that is universal for hearing aid users, cochlear implant users, and hearing-normal people. Thanks to its modularized design with telecoil technology. You can wear it no matter if you wear both sides of aids, single-side, or just no aid. The telecoil technology is an old technology which provides high sound quality and compatible with most of the hearing aid and cochlear implant products.
              </p>
            </div>
            <Art>
              <Img fluid={data.art_overview.childImageSharp.fluid} />
            </Art>
          </Grid>
        </Container>
      </Section>
    )}
  />
);

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 40px;
  text-align: left;
  align-items: center;
  justify-items: center;
  margin: 104px 0;

  ${props =>
    props.inverse &&
    `
    text-align: left;
    grid-template-columns: 2fr 1fr;
  `}

  h3 {
    margin-bottom: 16px;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }

    ${props =>
      props.inverse &&
      `
        ${Art} {
          order: 2;
        }
    `}
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
`;

export default About;
